  mounted() {
    // 在 mounted 中构建，防止created 中无法获取标签元素
    const data = this.data
    const scatterPlot = new Scatter('container', {
      appendPadding: 10,
      data,
      xField: 'Revenue',
      yField: 'Rating',
      shape: 'circle',
      colorField: 'Genre',
      size: 4,
      yAxis: {
        nice: true,
        line: {
          style: {
            stroke: '#aaa'
          }
        }
      },
      xAxis: {
        min: -100,
        grid: {
          line: {
            style: {
              stroke: '#eee'
            }
          }
        },
        line: {
          style: {
            stroke: '#aaa'
          }
        }
      }
    })
    scatterPlot.render()

    this.pointFun()
  },
  methods: {
    pointFun() {
      const data = [
        { x: 1, y: 4.181 },
        { x: 2, y: 4.665 },
