        { x: 25, y: 7.143 },
        { x: 26, y: 6.914 },
        { x: 27, y: 6.941 },
        { x: 28, y: 7.226 },
        { x: 29, y: 6.898 },
        { x: 30, y: 7.392 },
        { x: 31, y: 6.938 }
      ]
      const scatterPlot = new Scatter('containerG2', {
        data,
        xField: 'x',
        yField: 'y',
        size: 5,
        pointStyle: {
          stroke: '#777777',
          lineWidth: 1,
          fill: '#5B8FF9'
